export default {
	data() {
		return {
			title: 'Hello',
			userlist:'',//用户基本信息
			storeOrdersOne:'',//本地仓数据
			yunOrdersOne:'',//云仓数据
			sum:'',//总和
			key:1
		}
	},
	onLoad(e) {
		console.log(e);
		this.key = e.key;
		this.userlist = uni.getStorageSync('userlist'); //加载用户缓存
		console.log(this.userlist);
		this.Settlement();
	},
	methods: {
		// 本月结算
		Settlement() {
			this.utils.showloading();
			// uni.showLoading();
			this.http.getApi('/order/MonthGetOrder',{myId:this.userlist.id,status:this.key},'get',this.userlist.storeOpendid).then(res=>{
				console.log(res);
				this.sum = res.data.sum;
				this.storeOrdersOne = res.data.storeOrdersOne;
				this.yunOrdersOne = res.data.yunOrdersOne;
				// this.sum = res.rosePoint +res.sumPoint +res.monthPoint;
				// uni.hideLoading();
				this.utils.success('查询成功！',()=>{
					// this.utils.navback();
					uni.hideLoading();
				})
			}).catch(err=>{
				uni.hideLoading();
				uni.showToast({
					title: "暂无信息！",
					icon: "none",
					duration: 3000
				});
				console.log(err);
			})
			
		}
	}
}
